.device {width: 50%; margin-top: 3%; margin-left: 3%;}

.txt {width: 45%; float: right; margin-top: 4.5%;}
/* .txt p {height: 50px; line-height: 50px;} */

h3 {margin-bottom: 1rem;}
h3:first-child {margin-bottom: 0.5rem; font-size: 1rem; font-weight: inherit; color: #008db7;}

.concept p {margin-bottom: 1rem;}
.concept p:first-child {margin-bottom: 0.5rem; color: #008db7;}

.date p {margin-bottom: 1rem;}
.date p:first-child {margin-bottom: 0.5rem; color: #008db7;}

.color {margin-bottom: 1rem;}
.color p {margin-bottom: 0.5rem; color: #008db7;}
.color span {width: 50px; height: 50px; line-height: 50px; margin-right: 15px; border-radius: 25px; text-align: center; }
.yellow {background: #fbbd1a;}
.l_yellow {background: #ffe07d;}
.black {background: #231f20; color: azure;}
.gray {background: #eaeaea;}
.navy {background: #002c59; color: azure;}

.font p {margin-bottom: 1rem;}
.font p:first-child {margin-bottom: 0.5rem; color: #008db7;}

.program {margin-bottom: 1.2rem;}
.program p {margin-bottom: 0.5rem; color: #008db7;}
.program img {width: 45px; margin-right: 15px; vertical-align: middle;}

.link p {width: 105px; height: 30px; line-height: 30px; background: #008db7; text-align: center; border-radius: 10px; color: azure;}
.link p:hover {background: #a0cddb; color: #008db7;}