span {background: #008db7; width: 100%; height: 4rem; display: inline-block;}
.menu {font-size: 1.5rem; position: fixed; top: 1rem; left: 50%; transform: translateX(-50%); color: azure;}
.menu li {display: inline-block; margin-right: 100px; cursor: pointer;}
.menu li:last-child {margin-right: inherit;}
.menu li:hover {color: #0a4655;}