.skills {margin-top: 5%;}
.skills span {background: inherit; height: inherit; width: 30px; position: relative; left: 315px; bottom: 6px; z-index: 5;}
.skills p {text-indent: 10px; margin-bottom: 0.5rem; width: 100px;}
.skills h3 {margin-bottom: 2rem; text-indent: 10px;}

.progress {margin-bottom: 2rem; appearance: none;}
.progress::-webkit-progress-bar {background: #b3dfecc2; border-radius: 15px; height: 30px; width: 350px; overflow: hidden; position: relative; right: 30px;}
.progress::-webkit-progress-value {background: #008db7;}
